/**
 * Update the sample icon to match the value entered in the icon input
 * @param {FocusEvent} event
 */
const blurHandler = event => {
  const target = event.target || event.srcElement; // the element that blurred
  const icon = target.nextElementSibling; // the sample icon
  target.value = target.value.trim(); // trim whitespace
  if ( target.value.match( /^fa-[-a-z]+$/ ) ) { // if it's (likely to be) a valid icon class
    icon.className = 'fa fa-2x ' + target.value; // apply the class to the sample icon
  }
  else { // if it's not a valid icon class name
    icon.className = ''; // hide the icon
  }
};

// add the blur handler to all the info box widgets on the page at page load time
jQuery( '.info_box_widget_icon' ).blur( blurHandler );

// add the blur handler to new widgets when they're added
jQuery( document ).on( 'widget-added', ( event, $widget ) => {
  $widget.find( '.info_box_widget_icon' ).blur( blurHandler );
} );
